.task {
  display: grid;
  grid-template-columns: 80% 20%;
  position: relative;
  padding:10px;
  height: auto; 
  margin: auto;
  margin-bottom: 8px;
  width: 60%;
  font-family: 'Lato', sans-serif;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 1.4em;
  font-weight: bold;
  color: black;
  background: #abbcda;
  border-radius: 3px;
  opacity: 0.7;
  text-align: center; 
}
.task-done{
  display: grid;
  grid-template-columns: 80% 20%;
  background: #cccccc;
  padding: 10px;
  height: auto; 
  margin: auto;
  margin-bottom: 8px;
  width: 60%;
  font-family: 'Lato', sans-serif;
  font-family: 'Open Sans Condensed', sans-serif;
  font-size: 1.4em;
  font-weight: bold;
  color: black;  
  border-radius: 3px;
  -webkit-box-shadow: 43x 3px 2px #333;
          box-shadow: 43x 3px 2px #333;
  opacity: 0.7;
  text-align: center;
}
.noLine{
   overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  text-decoration: none;  
}
.lineThrough{
   overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  text-decoration: line-through;  
}
.task:hover{
  -webkit-filter: brightness(110%);
          filter: brightness(110%);  
}
.task-done:hover{
  -webkit-filter: brightness(110%);
          filter: brightness(110%);  
}
@-webkit-keyframes fadeIn { from { opacity:0; } to { opacity:0.8; }}
@keyframes fadeIn { from { opacity:0; } to { opacity:0.8; }}
.fade-in {
    opacity:0;
    -webkit-animation:fadeIn ease-in 1;
            animation:fadeIn ease-in 1;
    -webkit-animation-fill-mode:forwards;
            animation-fill-mode:forwards;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
}
.close,.tick{
    text-decoration: none;
    color: black;
    font-weight: bold;
    float:  right;
    cursor: pointer;
    font-size: 1em;
    border-radius: 5px;    
}
 .tick:hover {
    color: white;    
}
.close:hover{
    color: red;
}


